<template>
    <div class="logFund">
        <el-dialog :title="dialog.title" :visible.sync="dialog.show"
                   :close-on-click-modal="false" :close-on-press-escape="false"
                   :modal-append-to-body="false">
            <div class="from">
                <el-form ref="form" :model="form" :rules="form_rules" label-width="120px"
                         style="margin:10px;width:auto;">
                    <el-form-item label="">
                        <el-select v-model="form.type" placeholder="">
                            <el-option v-for="(formType, index) in format_type_list" :key="index" :label="formType"
                                       :value="formType"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item prop="describe" label="">
                        <el-input type="describe" v-model="form.describe"></el-input>
                    </el-form-item>
                    <el-form-item prop='income' label=":">
                        <el-input type="income" v-model="form.income"></el-input>
                    </el-form-item>
                    <el-form-item prop='expend' label=":">
                        <el-input type="expend" v-model="form.expend"></el-input>
                    </el-form-item>
                    <el-form-item prop='cash' label=":">
                        <el-input type="cash" v-model="form.cash"></el-input>
                    </el-form-item>
                    <el-form-item label=":">
                        <el-input type="textarea" v-model="form.remark"></el-input>
                    </el-form-item>
                    <el-form-item class="text_right">
                        <el-button @click="dialog.show = false"></el-button>
                        <el-button type="primary" @click="onSubmit('form')"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "DialogFound",
        // 
        components: {},
        //  function
        data() {
            return {
                format_type_list: [
                    "",
                    "",
                    "",
                    "",
                    "",
                    ""
                ],
                form_rules: {
                    describe: [
                        {required: true, message: "", trigger: "blur"}
                    ],
                    income: [
                        {required: true, message: "", trigger: "blur"}
                    ],
                    expend: [
                        {required: true, message: "", trigger: "blur"}
                    ],
                    cash: [{required: true, message: "", trigger: "blur"}]
                }
            };
        },
        props: {
            dialog: Object,
            form: Object
        },
        watch: {},
        // 
        computed: {},
        // props 
        methods: {
            onSubmit(from) {
                console.log('from', from);
                this.$refs[from].validate(valid => {
                    console.log('valid:', valid);
                    if (valid) {
                        const url = this.dialog.option === 'add' ? 'add' : `edit/${this.form.id}`;
                        this.$axios.post(`/api/profiles/${url}`, this.form).then(res => {
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            this.dialog.show = false;
                            this.$emit('update');
                        });
                    }
                })
            }
        },
        // data
        beforeCreate() {
        },
        // dom$el 
        created() {
        },
        //  render 
        beforeMount() {
        },
        // vm.$eldom
        mounted() {
        },
        //  DOM 
        beforeUpdate() {
        },
        //  DOM  DOM  DOM 
        updated() {
        },
        // keep-alive 
        activated() {
        },
        // keep-alive 
        deactivated() {
        },
        // 
        beforeDestroy() {
        },
        // Vue Vue 
        destroyed() {
        }
    }
</script>

<style scoped>

</style>